"use client";

import { useState } from "react";
import Link from "next/link";
import { QRCodeCanvas } from "qrcode.react";

export default function HostPage() {
  const [loading, setLoading] = useState(false);
  const [raid, setRaid] = useState<any>(null);

  async function createRaid() {
    setLoading(true);
    const res = await fetch("/api/raid/create", { method: "POST" });
    const json = await res.json();
    setLoading(false);
    if (json.error) return alert(json.error);
    setRaid(json.raid);
  }

  const url = raid ? `${window.location.origin}/raid/${raid.code}` : "";

  return (
    <main style={{ maxWidth: 1000, margin: "0 auto", padding: 24 }}>
      <h1 style={{ fontSize: 24, marginBottom: 12 }}>Host a Raid</h1>

      {!raid ? (
        <button className="btnPrimary" onClick={createRaid} disabled={loading}>
          {loading ? "Creating..." : "Create Raid"}
        </button>
      ) : (
        <div style={{ display: "grid", gap: 16 }}>
          <div className="card">
            <div style={{ fontSize: 14, opacity: 0.85 }}>Room Code</div>
            <div style={{ fontSize: 28, marginTop: 6 }}>{raid.code}</div>
            <div style={{ marginTop: 12 }}>
              <Link className="btnPrimary" href={`/raid/${raid.code}`}>
                Go to Raid Page
              </Link>
            </div>
          </div>

          <div className="card" style={{ display: "flex", gap: 18, alignItems: "center", flexWrap: "wrap" }}>
            <div>
              <div style={{ fontSize: 14, opacity: 0.85, marginBottom: 8 }}>QR Code (players scan this)</div>
              <div style={{ background: "white", padding: 10, borderRadius: 8, width: "fit-content" }}>
                <QRCodeCanvas value={url} size={180} />

              </div>
            </div>
            <div style={{ maxWidth: 520 }}>
              <div style={{ opacity: 0.8, lineHeight: 1.6 }}>
                <p><b>Host instructions:</b></p>
                <ol>
                  <li>Project this QR code.</li>
                  <li>Players join with name + 5-digit tag.</li>
                  <li>When ready, press <b>Start Battle</b> inside the raid.</li>
                </ol>
              </div>
              <div style={{ fontSize: 12, opacity: 0.7 }}>URL: {url}</div>
            </div>
          </div>
        </div>
      )}
    </main>
  );
}
